.reviewsContainer {
    width: 100%;
    margin: 0 auto;
    font-size: 12px;
}

.reviewsForm {
    border-radius: 5px;
    background-color: #fff;
    padding: 20px;
    width: 100%;
    box-sizing: border-box;
    position: relative;
    z-index: 2;
    margin: 20px 0px;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
}

label {
    display: inline-block;
    // margin-right: .5em;
    padding-top: 1.5em;
}

