@media screen and (min-width: 768px) {
    .reviewsForm {
        width: 500px;
    }
    .reviewsWrapper {
        margin: 0 auto;
    }
}

@media screen and (max-width: 768px) {
    .reviewsForm {
        margin: 20px auto;
    }
}

@media screen and (min-width: 1024px) {
    .reviewsWrapper {
        width: 1000px;
        padding: 20px 0px;
    }
    .reviewsForm {
        width: 500px;
    }
}
